AW;EACX,YAAY;EACZ,gBAAgB;EAChB,UAAU;EACV,kBAAkB;EAClB,UAAU;EAGV,UAAU;EAGV,wBAAwB;EACxB,qBAAqB;AFg2ZzB;AE9+ZE;EAEE,oBAAoB;EAGpB,MAAM;EACN,OAAO;EACP,YAAY;EACZ,WAAW;AF8+Zf;AE1+ZE;EACE,eAAe;EACf,aA3BgC;AFwgapC;AE/+ZE;EAOI,aAAa;AF4+ZnB;AEp+ZE;EACE,aAAa;EACb,kBAAkB;EAClB,aA1CsB;AFiha1B;AEn+ZE;EAGE,kBAAkB;EAClB,oBAAoB;EACpB,sBAAsB;EACtB,aApDsB;EAwDtB,aAAa;EACb,eAAe;EACf,gBAAgB;AFi+ZpB;AE99ZE;EAEE,kBAAkB;EAClB,MAAM;EACN,SAAS;EACT,OAAO;EACP,QAAQ;EAER,aApE+B;EAqE/B,oBAAoB;EACpB,wCAAwC;EACxC,0DAhEiE;EAiEjE,UAAU;AF+9Zd;AE3+ZE;EAeI,UAAU;AFg+ZhB;AE19ZM;EArBJ;IAsBM,YAAY;EF89ZlB;AACF;AE19ZE;EACE,+BArFsD;AFkja1D;AE19ZE;EAMI,UAAU;AFw9ZhB;AEl9ZE;EACE,kBAAkB;EAClB,aA1GsB;EA+GtB,aAAa;EAIb,sBAAsB;EAGtB,cAAc;EACd,eAAe;AF48ZnB;AEx8ZE;EACE,eAAe;EAKf,WAAW;EAKX,kBAAkB;AFm8ZtB;AE12ZE;EAA0C,IAAA;AF82Z5C;AE92ZE;EAA0C,IAAA;AF82Z5C;AE72ZE;EAAwC,IAAA;AFi3Z1C;AEj3ZE;EAAwC,IAAA;AFi3Z1C;AE/2ZE;EAEE,uDAA+C;UAA/C,+CAA+C;AFi3ZnD;AE92ZE;EAEE,qDAA6C;UAA7C,6CAA6C;AFg3ZjD;AE32ZE;EACE,YAAY;AF82ZhB;AEx2ZE;EAqBA,yBAAyB;EACzB,kCAAkC;EApBhC,uBAAuB;EACvB,2BAA2B;AF42Z/B;AEn2ZE;EASA,yBAAyB;EACzB,kCAAkC;EARhC,oBAAoB;AFu2ZxB;AEnvWE;EACE,kBAAkB;AFsvWtB;AEtyME;EACE,kBAAkB;AFyyMtB","sourcesContent":["/*\nTemplate Name: Material Pro Admin\nAuthor: Themedesigner\nEmail: niravjoshi87@gmail.com\nFile: scss\n*/\n/**\n * Table Of Content\n *\n * \t1. Globals\n *\t2. Headers\n *\t3. Navigations\n *\t4. Banners\n *\t5. Footers\n *\t6. app\n *\t7. Widgets\n *\t8. Custom Templates\n */\n\n@import 'variable';\n\n\n@import 'icons/themify-icons/themify-icons.css';\n@import 'icons/font-awesome/css/font-awesome.css';\n@import 'spinner';\n@import 'app';\n@import 'header';\n@import 'widgets';\n@import 'pages';\n@import 'sidebar';\n@import 'dark-sidebar';\n@import 'responsive';\n@import 'rtl';\n\n\n// Red Theme\n\n.danger {  \n\t@import 'colors/red';\n\t@include angular-material-theme($red-app-theme);\n}\n\n// Blue Theme\n\n.blue { \n\t@import 'colors/blue'; \n  \t@include angular-material-theme($blue-app-theme);\n}\n\n// Green Theme\n\n.green {  \n\t@import 'colors/green';\n  \t@include angular-material-theme($green-app-theme);\n}\n\n// Dark Theme\n\n.dark { \n\t@import 'colors/dark'; \n  \t@include angular-material-theme($dark-theme); \n}\n  \n\n$custom-typography: mat-typography-config( \n  $font-family: 'Poppins'\n); \n@include mat-core($custom-typography);","/*\nTemplate Name: Material Pro Admin\nAuthor: Themedesigner\nEmail: niravjoshi87@gmail.com\nFile: scss\n*/\n// font family, also paste this import into styles.css file.\n\n@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700');\n\n$bodyfont:'Poppins', sans-serif;\n$headingfont:'Poppins', sans-serif; \n\n@import '~@angular/material/theming';\n\n/*Material Theme Colors*/\n\n$primary: mat-palette($mat-blue, 600);\n$accent: mat-palette($mat-indigo, 500);\n$warn: mat-palette($mat-pink, 500);\n$theme: mat-light-theme($primary, $accent, $warn);\n$background: map-get($theme, background);\n$foreground: map-get($theme, foreground);\n\n// Default Theme\n@include angular-material-theme($theme);\n\n\n// Dark Theme\n\n/*Theme Colors*/\n \n$topbar: #2196f3;\n$sidebar: #fff;\n$sidebar-white: #e5edef;\n$sidebar-alt:#f2f6f8;\n$bodycolor: #eef5f9;\n$headingtext: #455a64;\n$bodytext: #67757c; \n$sidebar-text: #8f999e;\n$sidebar-icons: #99abb4 ;\n\n$font-16: 16px;\n\n$light-text: #a6b7bf;\n$themecolor: #1e88e5;\n$themecolor-alt: #26c6da;\n$themecolor-dark: #028ee1;\n\n/*bootstrap Color*/\n$danger: #fc4b6c;\n$success: #26c6da;\n$warning: #ffb22b;\n$primary: #7460ee;\n$info: #1e88e5;\n$inverse: #2f3d4a;\n$muted: #99abb4;\n$dark: #263238;\n$light: #f2f4f8;\n$extra-light: #ebf3f5;\n$secondary: #727b84;\n\n/*Light colors*/\n$light-danger: #f9e7eb;\n$light-success: #e8fdf8;\n$light-warning: #fff8ec;\n$light-primary: #f1effd;\n$light-info: #e3f3fd;\n$light-inverse: #f6f6f6;\n$light-megna: #e0f2f4; \n\n\n\n$danger-dark: #e6294b;\n$success-dark: #1eacbe;\n$warning-dark: #e9ab2e;\n$primary-dark: #6352ce;\n$info-dark: #028ee1;\n$red-dark: #d61f1f;\n$inverse-dark: #232a37;\n$dark-transparent:rgba(0, 0, 0, 0.05);\n\n/*Normal Color*/\n$white: #ffffff;\n$red: #fb3a3a;\n$yellow: #a0aec4;\n$purple: #7460ee;\n$blue: #02bec9;\n$megna: #00897b;\n\n/*Extra Variable*/\n$rgt: right;\n$lft: left;\n$border: rgba(120, 130, 140, 0.13);\n$table-border:#f3f1f1;\n$card-brd:#d7dfe3;\n$dark-text: #848a96;\n$radius: 4px;\n$form-brd: #d9d9d9;\n\n","// File for which all imports are resolved and bundled. This is the entry-point for\n// the `@angular/material` theming Sass bundle. See `//src/material:theming_bundle`.\n\n// Import all the theming functionality.\n// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: 1000 !default;\n$cdk-z-index-overlay: 1000 !default;\n$cdk-z-index-overlay-backdrop: 1000 !default;\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.32) !default;\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container, .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // The container should be the size of the viewport.\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n  }\n\n  // The overlay-container is an invisible element which contains all individual overlays.\n  .cdk-overlay-container {\n    position: fixed;\n    z-index: $cdk-z-index-overlay-container;\n\n    &:empty {\n      // Hide the element when it doesn't have any child nodes. This doesn't\n      // include overlays that have been detached, rather than disposed.\n      display: none;\n    }\n  }\n\n  // We use an extra wrapper element in order to use make the overlay itself a flex item.\n  // This makes centering the overlay easy without running into the subpixel rendering\n  // problems tied to using `transform` and without interfering with the other position\n  // strategies.\n  .cdk-global-overlay-wrapper {\n    display: flex;\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  // A single overlay pane.\n  .cdk-overlay-pane {\n    // Note: it's important for this one to start off `absolute`,\n    // in order for us to be able to measure it correctly.\n    position: absolute;\n    pointer-events: auto;\n    box-sizing: border-box;\n    z-index: $cdk-z-index-overlay;\n\n    // For connected-position overlays, we set `display: flex` in\n    // order to force `max-width` and `max-height` to take effect.\n    display: flex;\n    max-width: 100%;\n    max-height: 100%;\n  }\n\n  .cdk-overlay-backdrop {\n    // TODO(jelbourn): reuse sidenav fullscreen mixin.\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n\n    z-index: $cdk-z-index-overlay-backdrop;\n    pointer-events: auto;\n    -webkit-tap-highlight-color: transparent;\n    transition: opacity $backdrop-animation-duration $backdrop-animation-timing-function;\n    opacity: 0;\n\n    &.cdk-overlay-backdrop-showing {\n      opacity: 1;\n\n      // In high contrast mode the rgba background will become solid so we need to fall back\n      // to making it opaque using `opacity`. Note that we can't use the `cdk-high-contrast`\n      // mixin, because we can't normalize the import path to the _a11y.scss both for the\n      // source and when this file is distributed. See #10908.\n      @media screen and (-ms-high-contrast: active) {\n        opacity: 0.6;\n      }\n    }\n  }\n\n  .cdk-overlay-dark-backdrop {\n    background: $cdk-overlay-dark-backdrop-background;\n  }\n\n  .cdk-overlay-transparent-backdrop {\n    // Note: as of Firefox 57, having the backdrop be `background: none` will prevent it from\n    // capturing the user's mouse scroll events. Since we also can't use something like\n    // `rgba(0, 0, 0, 0)`, we work around the inconsistency by not setting the background at\n    // all and using `opacity` to make the element transparent.\n    &, &.cdk-overlay-backdrop-showing {\n      opacity: 0;\n    }\n  }\n\n  // Overlay parent element used with the connected position strategy. Used to constrain the\n  // overlay element's size to fit within the viewport.\n  .cdk-overlay-connected-position-bounding-box {\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n\n    // We use `display: flex` on this element exclusively for centering connected overlays.\n    // When *not* centering, a top/left/bottom/right will be set which overrides the normal\n    // flex layout.\n    display: flex;\n\n    // We use the `column` direction here to avoid some flexbox issues in Edge\n    // when using the \"grow after open\" options.\n    flex-direction: column;\n\n    // Add some dimensions so the element has an `innerText` which some people depend on in tests.\n    min-width: 1px;\n    min-height: 1px;\n  }\n\n  // Used when disabling global scrolling.\n  .cdk-global-scrollblock {\n    position: fixed;\n\n    // Necessary for the content not to lose its width. Note that we're using 100%, instead of\n    // 100vw, because 100vw includes the width plus the scrollbar, whereas 100% is the width\n    // that the element had before we made it `fixed`.\n    width: 100%;\n\n    // Note: this will always add a scrollbar to whatever element it is on, which can\n    // potentially result in double scrollbars. It shouldn't be an issue, because we won't\n    // block scrolling on a page that doesn't have a scrollbar in the first place.\n    overflow-y: scroll;\n  }\n}\n\n@mixin cdk-a11y {\n  .cdk-visually-hidden {\n    border: 0;\n    clip: rect(0 0 0 0);\n    height: 1px;\n    margin: -1px;\n    overflow: hidden;\n    padding: 0;\n    position: absolute;\n    width: 1px;\n\n    // Avoid browsers rendering the focus ring in some cases.\n    outline: 0;\n\n    // Avoid some cases where the browser will still render the native controls (see #9049).\n    -webkit-appearance: none;\n    -moz-appearance: none;\n  }\n}\n\n/// Emits the mixin's content nested under `$selector-context` if `$selector-context`\n/// is non-empty.\n/// @param selector-context The selector under which to nest the mixin's content.\n@mixin _cdk-optionally-nest-content($selector-context) {\n  @if ($selector-context == '') {\n    @content;\n  }\n  @else {\n    #{$selector-context} {\n      @content;\n    }\n  }\n}\n\n/// Applies styles for users in high contrast mode. Note that this only applies\n/// to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n/// attribute, however Chrome handles high contrast differently.\n///\n/// @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n///    `white-on-black` or `black-on-white`.\n/// @param encapsulation Whether to emit styles for view encapsulation. Values are:\n///     * `on` - works for `Emulated`, `Native`, and `ShadowDom`\n///     * `off` - works for `None`\n///     * `any` - works for all encapsulation modes by emitting the CSS twice (default).\n@mixin cdk-high-contrast($target: active, $encapsulation: 'any') {\n  @if ($target != 'active' and $target != 'black-on-white' and $target != 'white-on-black') {\n    @error 'Unknown cdk-high-contrast value \"#{$target}\" provided. ' +\n           'Allowed values are \"active\", \"black-on-white\", and \"white-on-black\"';\n  }\n\n  @if ($encapsulation != 'on' and $encapsulation != 'off' and $encapsulation != 'any') {\n    @error 'Unknown cdk-high-contrast encapsulation \"#{$encapsulation}\" provided. ' +\n           'Allowed values are \"on\", \"off\", and \"any\"';\n  }\n\n  // If the selector context has multiple parts, such as `.section, .region`, just doing\n  // `.cdk-high-contrast-xxx #{&}` will only apply 