na;\n}\n\n.text-dark {\n    color: $bodytext;\n}\n.text-themecolor {\n    color: $themecolor;\n}\n    .text-center{\n        text-align: center;\n    }\n    .text-right{\n        text-align: right;\n        \n    }\n    .text-left{\n        text-align: left;\n    }\n}\n/*******************\nBackground Colors\n*******************/\n\n.bg-primary {\n    background-color: $primary !important;\n}\n\n.bg-success {\n    background-color: $success !important;\n}\n\n.bg-info {\n    background-color: $info !important;\n}\n\n.bg-warning {\n    background-color: $warning !important;\n}\n\n.bg-danger {\n    background-color: $danger !important;\n}\nhtml body {\n.bg-megna {\n    background-color: $megna;\n}\n.bg-theme {\n    background-color: $themecolor;\n}\n\n.bg-inverse {\n    background-color: $inverse;\n}\n\n.bg-purple {\n    background-color: $purple!important;\n}\n\n.bg-light-primary {\n    background-color: $light-primary;\n}\n\n.bg-light-success {\n    background-color: $light-success;\n}\n\n.bg-light-info {\n    background-color: $light-info;\n}\n.bg-light-extra {\n    background-color: $extra-light;\n}\n\n.bg-light-warning {\n    background-color: $light-warning;\n}\n\n.bg-light-danger {\n    background-color: $light-danger;\n}\n\n.bg-light-inverse {\n    background-color: $light-inverse;\n}\n.bg-light {\n    background-color: $light;\n}\n.bg-white {\n    background-color: $white;\n}\n    }\n.img-circle{\n    border-radius: 100%;\n}\n.no-shadow{\n    box-shadow: none;\n}\nhr{\n    border: 0px;\n    height:1px;\n    background: $border;\n}\n/*******************\nRounds\n*******************/\n.round{\n    line-height:48px;\n    color: $white;\n    width: 45px;\n    height: 45px;\n    display: inline-block;\n    font-weight: 400;\n    text-align: center;\n    border-radius: 100%;\n    background: $info;\n    img{\n        border-radius: 100%;\n    }\n}\n.round-lg{\n    line-height:65px;\n    width: 60px;\n    height: 60px;\n    font-size:30px;\n}\n.round.round-info{\n    background: $info;\n}\n.round.round-warning{\n    background: $warning;\n}\n.round.round-danger{\n    background: $danger;\n}\n.round.round-success{\n    background: $success;\n}\n.round.round-primary{\n    background: $primary;\n}\n/*******************\nLabels\n*******************/\n\n.label {\n    padding: 3px 10px;\n    line-height: 13px;\n    color: $white;\n    font-weight: 400;\n    border-radius: $radius;\n    font-size: 75%;\n}\n\n.label-rounded {\n    border-radius: 60px;\n}\n\n.label-custom {\n    background-color: $megna;\n}\n\n.label-success {\n    background-color: $success;\n}\n\n.label-info {\n    background-color: $info;\n}\n\n.label-warning {\n    background-color: $warning;\n}\n\n.label-danger {\n    background-color: $danger;\n}\n\n.label-megna {\n    background-color: $megna;\n}\n\n.label-primary {\n    background-color: $primary;\n}\n\n.label-purple {\n    background-color: $purple;\n}\n\n.label-red {\n    background-color: $red;\n}\n\n.label-inverse {\n    background-color: $inverse;\n}\n\n.label-default {\n    background-color: $light;\n}\n\n.label-white {\n    background-color: $white;\n}\n\n\n.label-light-success {\n    background-color: $light-success;\n    color:$success;\n}\n\n.label-light-info {\n    background-color: $light-info;\n    color:$info;\n}\n\n.label-light-warning {\n    background-color: $light-warning;\n    color:$warning;\n}\n\n.label-light-danger {\n    background-color: $light-danger;\n    color:$danger;\n}\n\n.label-light-megna {\n    background-color: $light-megna;\n    color:$megna;\n}\n\n.label-light-primary {\n    background-color: $light-primary;\n    color:$primary;\n}\n\n.label-light-inverse {\n    background-color: $light-inverse;\n    color:$inverse;\n}\n\n\n\n/*******************\nNotify\n*******************/\n\n.notify {\n    position: relative;\n    top: -20px;\n    right:0px;\n    .heartbit {\n        position: absolute;\n        top: -20px;\n        right: -4px;\n        height: 25px;\n        width: 25px;\n        \n        border: 5px solid $danger;\n        border-radius: 70px;\n        -moz-animation: heartbit 1s ease-out;\n        -moz-animation-iteration-count: infinite;\n        -o-animation: heartbit 1s ease-out;\n        -o-animation-iteration-count: infinite;\n        -webkit-animation: heartbit 1s ease-out;\n        -webkit-animation-iteration-count: infinite;\n        animation-iteration-count: infinite;\n    }\n    .point {\n        width: 6px;\n        height: 6px;\n        -webkit-border-radius: 30px;\n        -moz-border-radius: 30px;\n        border-radius: 30px;\n        background-color: $danger;\n        position: absolute;\n        right: 6px;\n        top: -10px;\n    }\n}\n\n@-moz-keyframes heartbit {\n    0% {\n        -moz-transform: scale(0);\n        opacity: 0.0;\n    }\n    25% {\n        -moz-transform: scale(0.1);\n        opacity: 0.1;\n    }\n    50% {\n        -moz-transform: scale(0.5);\n        opacity: 0.3;\n    }\n    75% {\n        -moz-transform: scale(0.8);\n        opacity: 0.5;\n    }\n    100% {\n        -moz-transform: scale(1);\n        opacity: 0.0;\n    }\n}\n\n@-webkit-keyframes heartbit {\n    0% {\n        -webkit-transform: scale(0);\n        opacity: 0.0;\n    }\n    25% {\n        -webkit-transform: scale(0.1);\n        opacity: 0.1;\n    }\n    50% {\n        -webkit-transform: scale(0.5);\n        opacity: 0.3;\n    }\n    75% {\n        -webkit-transform: scale(0.8);\n        opacity: 0.5;\n    }\n    100% {\n        -webkit-transform: scale(1);\n        opacity: 0.0;\n    }\n}\n.btn-block{\n    display: block;\n    width: 100%;\n}\n.btn-lg{\n    line-height: 48px!important;\n}\n/*******************\nfroms\n*******************/\n.form-control{\n    border: 0px;\n    line-height: 25px;\n    padding: 10px;\n    width: 100%;\n    font-size:$font-16;\n}\n.mat-form-field-underline{\n    background-color: rgba(0, 0, 0, 0.25);\n}\n.mat-form-field{\n   width: 100%;\n}\n/*******************\nMat card\n*******************/\n.mat-card{\n    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.05)!important;  \n    -webkit-box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.05)!important; \n    margin: 15px;\n    padding: 0px!important;\n    border-radius: $radius;\n    \n    .mat-card-content{\n        font-size:1rem;\n        line-height: 23px;\n        padding: 24px;    \n    }\n    .mat-card-image {\n        width: calc(100% + 0px);\n        margin: 0 0px 0px 0px;\n    }\n    .mat-card-header{\n        padding: 12px;\n        border-radius: $radius $radius 0 0;\n        .mat-card-title{\n            margin-bottom: 2px;\n            font-size: 20px;\n        }\n        .mat-card-subtitle, .mat-card-subtitle:not(:first-child){\n            margin-bottom: 0px;\n            margin-top: 0px;\n            font-size: 14px;\n        }\n        &.text-white{\n           .mat-card-title{\n            color:$white;\n            }\n            .mat-card-subtitle{\n                color:rgba(255, 255, 255, 0.7);\n            }\n        }\n    }\n    .mat-card-title{\n        font-size: 20px;\n        margin-bottom: 5px;\n    }\n    .mat-card-subtitle{\n        margin-bottom: 20px;\n    }\n    .mat-card-actions{\n        margin: 0px!important;\n    }\n}\n\n/*******************\nGrid\n*******************/\n.row{\n    margin: 0 -10px;\n}\n/*******************\nTable\n*******************/\n.table{\n    width: 100%;\n    text-align: left;\n}\n.table-rasponsive{\n    overflow:auto;\n    .mat-table{\n        min-width: 280px;\n    }\n    .mat-header-row{\n        .mat-header-cell{\n            white-space: nowrap;\n        }\n    }\n    .mat-cell{\n            white-space: nowrap;\n        }\n}\n/*******************\nSparkline chart tooltip\n*******************/\n.jqstooltip{\n  width: auto !important;\n  height: auto !important;\n  background: $inverse;\n  color: $white;\n  padding: 5px 15px; \n  border: 0px;\n  border-radius: 60px;     \n}\n/*******************\nlist inline\n*******************/\n.list-inline{\n    margin-left: 0px;\n    padding: 0px;\n    li{\n        display: inline-block;\n        list-style: none;\n        margin-right: 10px;\n        &:last-child{\n            margin-right: 0px;\n        }\n        \n    }\n}\n.button-row button{\n    margin-right:2px;\n    margin-left:2px;\n}","/*\nTemplate Name: Material Pro Admin\nAuthor: Wrappixel\nEmail: niravjoshi87@gmail.com\nFile: scss\n*/\n\n\n/*******************\n Topbar\n*******************/\nmat-toolbar{\n    box-shadow: 1px 0px 10px rgba(0, 0, 0, 0.5);\n    \n    &.topbar{\n        padding: 0px 15px;\n    }\n    .navbar-header {\n        \n        text-align: center;\n        .navbar-brand {\n            display: flex;\n            line-height: 18px;\n            \n            align-items: center;\n            .dark-logo {\n                display: none;\n                \n            }\n            b {\n               margin-right: 5px;\n            }\n            img{\n                \n            }\n        }\n    }\n    \n    .app-search{\n        display: none;\n        width: 100%;\n        top: 0px;\n        left: 0px;\n        z-index: 1;\n        position: absolute;\n        .form-control{\n            line-height: 42px;\n            \n        }\n        .cl-srh-btn{\n            position: absolute;\n            top: 12px;\n            right: 15px;\n            font-size: 15px;\n            color:$bodytext;\n            cursor: pointer\n        }\n    }\n    .profile-pic{\n        width: 40px;\n        height: 40px;\n        border-radius: 100%;\n    }\n}\n/*******************\n Topbar\n*******************/\n.topbar {\n  position: relative;\n  /* Make sure the toolbar will stay on top of the content as it scrolls past. */\n  z-index: 2;\n}\n\n.example-sidenav-container {\n  /* When the sidenav is not fixed, stretch the sidenav container to fill the available space. This\n     causes `<mat-sidenav-content>` to act as our scrolling element for desktop layouts. */\n  flex: 1;\n}\n\n.example-sidenav-container {\n  /* When the sidenav is fixed, don't constrain the height of the sidenav container. This allows the\n     `<body>` to be our scrolling element for mobile layouts. */\n  flex: 1 0 auto;\n}\n\n","/*\nTemplate Name: Material Pro Admin\nAuthor: Themedesigner\nEmail: niravjoshi87@gmail.com\nFile: scss\n*/\n\n\n/*******************\n/*User mail widgets*/\n/*******************/\n.mailbox {\n    width: 280px;\n    ul { margin: 0px;\n            padding: 0px;\n            li {\n                list-style: none;\n            }\n        }\n    ul li {\n        .drop-title {\n            font-weight: 500;\n            padding: 11px 15px 15px;\n            border-bottom: 1px solid $border;\n        }\n        .nav-link {\n            border-top: 1px solid $border;\n            padding-top: 15px;\n        }\n    }\n    .message-center {\n        height: 230px;\n        overflow: auto;\n        position: relative;\n        a {\n            border-bottom: 1px solid $border;\n            display: flex;\n            color:$headingtext;\n            align-items: center;\n            text-decoration: none;\n            padding: 9px 15px;\n            &:hover {\n                background: $light;\n            }\n            div {\n                white-space: normal;\n            }\n            .round{\n                flex-shrink: 0;\n                margin: 0 10px 0px 0;\n            }\n            .mail-content{\n                overflow: hidden;\n            }\n            .user-img {\n                width: 40px;\n                position: relative;\n                margin: 0 10px 0px 0;\n                img {\n                    width: 100%;\n                }\n                .profile-status {\n                    border: 2px solid $white;\n                    border-radius: 50%;\n                    display: inline-block;\n                    height: 10px;\n                    left: 30px;\n                    position: absolute;\n                    top: 1px;\n                    width: 10px;\n                }\n                .online {\n                    background: $success;\n                }\n                .busy {\n                    background: $danger;\n                }\n                .away {\n                    background: $warning;\n                }\n                .offline {\n                    background: $warning;\n                }\n            }\n            .mail-content {\n\n                h5 {\n                    margin: 5px 0px 0;\n                }\n                .mail-desc,\n                .time {\n                    font-size: 12px;\n                    display: block;\n                    margin: 1px 0;\n                    text-overflow: ellipsis;\n                    overflow: hidden;\n                    color: $bodytext;\n                    white-space: nowrap;\n                }\n            }\n        }\n    }\n}\n\n/*******************/\n/*Comment widgets*/\n/*******************/\n\n.comment-widgets {\n    position: relative;\n    margin-bottom: 10px;\n    .comment-row {\n        border-left: 3px solid $white;\n        padding: 15px;\n        &:hover,\n        &.active {\n            border-color: $themecolor;\n        }\n    }\n}\n\n.comment-text {\n    padding: 15px 15px 15px 20px;\n    &:hover .comment-footer,\n    &.active .comment-footer {\n        .action-icons {\n            visibility: visible;\n        }\n    }\n    p {\n        max-height: 50px;\n        overflow: hidden;\n    }\n}\n\n.comment-footer {\n    .action-icons {\n        visibility: hidden;\n        a {\n            padding-left: 15px;\n            vertical-align: middle;\n            color: $muted;\n            &:hover,\n            &.active {\n                color: $info;\n            }\n        }\n    }\n}\n\n\n\n/*******************/\n/*Feed widget*/\n/*******************/\n\n.feeds {\n    margin: 0px;\n    padding: 0px;\n    li {\n        list-style: none;\n        padding: 10px;\n        display: block;\n        &:hover {\n            background: $extra-light;\n        }\n        > div {\n            width: 40px;\n            height: 40px;\n            margin-right: 5px;\n            display: inline-block;\n            text-align: center;\n            vertical-align: middle;\n            border-radius: 100%;\n            i {\n                line-height: 40px;\n            }\n        }\n        span {\n            float: right;\n            width: auto;\n            font-size: 12px;\n        }\n    }\n}\n\n\n/*******************/\n/*social-widgets*/\n/*******************/\n\n.social-widget {\n    .soc-header {\n        padding: 15px;\n        text-align: center;\n        font-size: 36px;\n        color: #fff;\n        &.box-facebook {\n            background: #3b5998;\n        }\n        &.box-twitter {\n            background: #00aced;\n        }\n        &.box-google {\n            background: #f86c6b;\n        }\n        &.box-linkedin {\n            background: #4875b4;\n        }\n    }\n    .soc-content {\n        display: flex;\n        text-align: center;\n        div {\n            padding: 10px;\n            h3 {\n                margin-bottom: 0px;\n            }\n        }\n    }\n}\n\n\n.message-widget{\n    width: 100%;\n    .message-center{\n        height: 100%;\n    }\n}\n\n/*******************/\n/*social-profile-first*/\n/*******************/\n.little-profile{\n  .pro-img, .pro-img:first-child {\n    margin-top: -110px;\n    margin-bottom: 20px;\n  img {\n    width: 128px;\n    height: 128px;\n    -webkit-box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);\n    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);\n    border-radius: 100%;\n      }\n }\n}\n\n/*******************\nwidget personal-timeline scss\n******************/\nhtml .mat-list .personal-timeline{\n    height: auto;\n}\n","/*\nTemplate Name: Material Pro Admin\nAuthor: Wrappixel\nEmail: niravjoshi87@gmail.com\nFile: scss\n*/\n\n/*============================================================== \n For all pages \n ============================================================== */\n\n/*******************\n Main container\n*******************/\n\n.main-container {\n  display: flex;\n  flex-direction: column;\n  position: relative;\n  overflow: hidden;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n}\n\n.mat-drawer-container {\n  background: $bodycolor;\n}\n\nhtml .page-wrapper {\n  height: calc(100vh - 64px);\n  .page-content {\n    padding: 15px;\n  }\n}\n\n/*******************\n Login register\n*******************/\n.login-register {\n  height: 100vh;\n  display: flex;\n  background-size: cover;\n  align-items: center;\n  .mat-card {\n    margin: 0px 15px;\n  }\n  .mat-form-field {\n    width: 100%;\n  }\n  .login-register-box {\n    max-width: 400px;\n    min-width: 280px;\n    margin: 0 auto;\n    vertical-align: middle;\n  }\n  .support-text {\n    margin-top: -15px;\n    display: block;\n    padding-bottom: 15px;\n  }\n}\n.error-card {\n  h1 {\n    font-size: 210px;\n    font-weight: 900;\n    line-height: 210px;\n    margin: 0px;\n    text-shadow: 4px 5px 1px #b3bbbf;\n  }\n}\n\n.ngx-datatable.material {\n  box-shadow: none;\n  border: 1px solid $border;\n}\n/*******************\n Calendar page\n*******************/\n.cal-event-action {\n  text-decoration: none;\n  margin: 0 3px;\n}\n.cal-header {\n  border-left: 1px solid #e1e1e1;\n  border-right: 1px solid #e1e1e1;\n}\n\n.act {\n  color: white;\n\n  &:hover {\n    color: aqua;\n  }\n}\n/*******************\n Mail page\n*******************/\n.icon-box {\n  width: 20%;\n  display: inline-block;\n}\n/*******************\n Mail page\n*******************/\n.email-box {\n  border: 1px solid $border;\n}\n.email-sidebar {\n  .mat-drawer-inner-container{\n    overflow:visible;\n  }\n  border-right: 1px solid $border;\n  width: 300px;\n  .d-flex {\n    display: flex;\n    padding: 15px 0px;\n    cursor: pointer;\n  }\n  .selected {\n    .d-flex {\n      background: $light;\n    }\n  }\n  .icon-avatar {\n    width: 60px;\n    text-align: center;\n    flex-shrink: 0;\n  }\n  .mail-title {\n    font-weight: 500;\n  }\n  .text-ellipsis {\n    white-space: nowrap;\n    overflow: hidden;\n    max-width: 165px;\n    text-overflow: ellipsis;\n  }\n  .avatar {\n    width: 40px;\n    border-radius: 100%;\n  }\n}\n\n.email-right-panel {\n  padding: 30px;\n  background: $light;\n  .avatar-lg {\n    width: 100px;\n    border-radius: 100%;\n  }\n  .row {\n    overflow: hidden;\n  }\n  .col-md-2 {\n    width: 20%;\n    padding: 10px;\n    float: $lft;\n  }\n}\n.align-items-center {\n  align-items: center;\n}\n.bottom-right {\n  position: absolute !important;\n  right: 40px;\n  bottom: 40px;\n  z-index: 10;\n}\n.chat-right-panel {\n  .top-avatar {\n    img {\n      width: 40px;\n      vertical-align: middle;\n    }\n  }\n}\n.chat-middle-box {\n  border-left: 1px solid $border;\n  background: $white;\n  margin-bottom: 0px !important;\n}\n.chat-footer {\n  padding: 10px 15px !important;\n  background: $white;\n  border-top: 1px solid $border;\n  border-left: 1px solid $border;\n}\n.chat-list {\n  overflow: hidden;\n  box-sizing: border-box;\n  width: 100%;\n  margin-bottom: 1rem;\n  padding: 0 2rem;\n  display: flex;\n  &.even {\n    -webkit-box-pack: end;\n    -ms-flex-pack: end;\n    -moz-justify-content: flex-end;\n    justify-content: flex-end;\n    text-align: right;\n  }\n}\n.chat-block {\n  display: flex;\n  align-items: center;\n  padding: 15px;\n  background: $light;\n  border-radius: 6px;\n  img {\n    vertical-align: middle;\n    margin-right: 10px;\n  }\n}\n.chat-date {\n  font-size: 12px;\n  color: $muted;\n}\n\n/*******************\nChartistt chart css\n******************/\n.barchrt {\n  .ct-series-a .ct-bar {\n    stroke: $success;\n  }\n  .ct-series-b .ct-bar {\n    stroke: $info;\n  }\n}\n\n.linearea {\n  height: 280px;\n  .ct-series-a .ct-area {\n    fill-opacity: 0.05;\n    fill: $success;\n  }\n  .ct-series-a .ct-line,\n  .ct-series-a .ct-point {\n    stroke: $success;\n    stroke-width: 2px;\n  }\n  .ct-series-b .ct-area {\n    fill: $info;\n    fill-opacity: 0.1;\n  }\n  .ct-series-b .ct-line,\n  .ct-series-b .ct-point {\n    stroke: $info;\n    stroke-width: 2px;\n  }\n  .ct-series-c .ct-area {\n    fill: $danger;\n    fill-opacity: 0.1;\n  }\n  .ct-series-c .ct-line,\n  .ct-series-c .ct-point {\n    stroke: $danger;\n    stroke-width: 2px;\n  }\n  .ct-series-a .ct-point,\n  .ct-series-b .ct-point,\n  .ct-series-c .ct-point {\n    stroke-width: 6px;\n  }\n}\n.piechart {\n  .ct-series-a .ct-slice-donut {\n    stroke: $info;\n  }\n  .ct-series-b .ct-slice-donut {\n    stroke: $success;\n  }\n  .ct-series-c .ct-slice-donut {\n    stroke: $purple;\n  }\n  .ct-series-d .ct-slice-donut {\n    stroke: $light;\n  }\n  .ct-series-e .ct-slice-donut {\n    stroke: $warning;\n  }\n}\n\n/*******************\nPricing scss\n******************/\nhtml {\n  .popular-plan {\n    transform: scale(1.1);\n    z-index: 1;\n  }\n  .price-listing {\n    margin-bottom: 30px;\n    .mat-list-item {\n      height: auto;\n      .mat-list-item-content {\n        display: block;\n        padding: 20px 0;\n      }\n    }\n  }\n}\n/*******************\nChartis tooltip scss\n******************/\n.chartist-tooltip {\n  position: absolute;\n  display: inline-block;\n  opacity: 0;\n  min-width: 50px;\n  padding: 5px 10px;\n  border-radius: 5px;\n  background: #313131;\n  color: $white;\n  font-weight: 500;\n  text-align: center;\n  pointer-events: none;\n  z-index: 1;\n  -webkit-transition: opacity 0.2s linear;\n  -moz-transition: opacity 0.2s linear;\n  -o-transition: opacity 0.2s linear;\n  transition: opacity 0.2s linear;\n}\n\n/*.chartist-tooltip:before {\n    content: \"\";\n    position: absolute;\n    top: 100%;\n    left: 50%;\n    width: 0;\n    height: 0;\n    margin-left: -5px;\n    border: 5px solid transparent;\n    border-top-color: #313131; }\n*/\n.chartist-tooltip.tooltip-show {\n  opacity: 1;\n}\n\n.ct-area,\n.ct-line {\n  pointer-events: none;\n}\n\n/*# sourceMappingURL=chartist-plugin-tooltip.css.map */\n.no-wrap {\n  td, .ng-star-inserted{\n  white-space:nowrap;\n}\n}\n/*******************\nSmart table\n******************/\n.smart-table tr td,\n.smart-table tr th {\n  padding: 15px !important;\n  text-align:left;\n}\n.ng2-smart-action-add-add {\n  color: $white !important;\n  background: $themecolor;\n  padding: 8px 15px;\n  border-radius: $radius;\n  white-space: nowrap;\n}\n.ng2-smart-pagination-nav {\n  margin-left: auto;\n}\n.ng2-smart-pagination-nav .pagination > li > a {\n  line-height: 1rem;\n}\n.ng2-smart-filter .form-control {\n  border: 1px solid $border;\n  border-radius: 0.25rem;\n  min-height: 38px;\n}\n.form-control{\n  border:1px solid $border;\n}\n/**********************\nOverall Responsive table\n***********************/\n\n.responsive-table {\n  display: flex;\n  flex-direction: column;\n  overflow:auto;\n  .mat-cell:first-of-type,\n  .mat-footer-cell:first-of-type,\n  .mat-header-cell:first-of-type {\n    padding-left: 0;\n  }\n  .header-label {\n    display: none;\n  }\n  .mat-table {\n    overflow: auto;\n    max-height: 100%;\n  }\n}\n.table-responsive{\n  overflow:auto;\n}\n.pagination {\n  display: -ms-flexbox;\n  display: flex;\n  padding-left: 0;\n  list-style: none;\n  border-radius: 0.25rem;\n}\n\n.page-link {\n  position: relative;\n  display: block;\n  padding: 0.5rem 0.75rem;\n  margin-left: -1px;\n  line-height: 1.25;\n  color: $themecolor;\n  background-color: $white;\n  border: 1px solid $border;\n}\n\n.page-link:hover {\n  z-index: 2;\n  color: $bodytext;\n  text-decoration: none;\n  background-color: $light;\n  border-color: $border;\n}\n\n.page-link:focus {\n  z-index: 2;\n  outline: 0;\n  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);\n}\n\n.page-link:not(:disabled):not(.disabled) {\n  cursor: pointer;\n}\n\n.page-item:first-child .page-link {\n  margin-left: 0;\n  border-top-left-radius: 0.25rem;\n  border-bottom-left-radius: 0.25rem;\n}\n\n.page-item:last-child .page-link {\n  border-top-right-radius: 0.25rem;\n  border-bottom-right-radius: 0.25rem;\n}\n\n.page-item.active .page-link {\n  z-index: 1;\n  color: $white;\n  background-color: $themecolor;\n  border-color: $themecolor;\n}\n\n.page-item.disabled .page-link {\n  color: $muted;\n  pointer-events: none;\n  cursor: auto;\n  background-color: $white;\n  border-color: $border;\n}\n","/*\nTemplate Name: Material Pro Admin\nAuthor: Themedesigner\nEmail: niravjoshi87@gmail.com\nFile: scss\n*/\n\n$mini-sidebar-width: 80px;\n/*******************\nMain sidebar\n******************/\n\n.mat-sidenav {\n  width: 240px;\n  padding-top: 0px;\n}\n#snav {\n  box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.08);\n  .mat-drawer-inner-container{\n    overflow:visible;\n  }\n  .mat-nav-list .mat-list-item {\n    height: auto;\n    &:hover {\n      background: none;\n    }\n\n    .mat-list-item-content {\n      display: block;\n      padding: 0px 0px;\n      &:hover {\n        background: none;\n        > a {\n          color: $themecolor;\n        }\n      }\n    }\n\n    a {\n      height: 45px;\n      padding: 0 10px;\n      margin-bottom: 10px;\n      align-items: center;\n      display: flex;\n      font-size: 15px;\n      white-space: nowrap;\n      color: $sidebar-text;\n      mat-icon:not(.dd-icon) {\n        margin-right: 8px;\n      }\n      mat-icon {\n        color: $sidebar-icons;\n      }\n      .dd-icon {\n        font-size: 16px;\n        width: 16px;\n        transition: 0.2s ease-in;\n        margin: 5px 0 5px 5px;\n        height: 16px;\n      }\n    }\n    &.selected > .mat-list-item-content > a {\n      background: $themecolor-alt;\n      border-radius: $radius;\n      color: $white;\n\n      mat-icon {\n        color: $white;\n      }\n      .dd-icon {\n        -webkit-transform: rotate(-180deg);\n        transform: rotate(-180deg);\n      }\n    }\n    &.selected .sub-item .mat-list-item-content a.selected {\n      background: transparent;\n      color: $inverse;\n      font-weight: 500;\n    }\n  }\n  .app-sidebar > .mat-nav-list > .mat-list-item > .mat-list-item-content {\n    padding: 0px 15px;\n  }\n  .mat-nav-list .sub-item {\n    display: none;\n    margin-top: -15px;\n    a {\n      padding-left: 40px;\n      height: 50px;\n      margin-bottom: 0px;\n    }\n    .child-sub-item {\n      display: none;\n      margin-top: -15px;\n      a {\n        padding: 0 0 0 55px;\n        height: 50px;\n        margin-bottom: 0px;\n      }\n    }\n  }\n  .saperator {\n    cursor: text;\n    font-size: 13px;\n    margin-bottom: 15px;\n    margin-top: 25px;\n    font-weight: 500;\n    white-space: nowrap;\n  }\n  .selected {\n    .sub-item {\n      display: block;\n      .selected + {\n        .child-sub-item {\n          display: block;\n        }\n      }\n    }\n  }\n}\n/*******************\nuse profile section\n******************/\n\n.user-profile {\n  position: relative;\n  background-size: cover;\n\n  .profile-img {\n    width: 50px;\n    margin-left: 30px;\n    padding: 31px 0;\n    border-radius: 100%;\n    &::before {\n      -webkit-animation: 2.5s blow 0s linear infinite;\n      animation: 2.5s blow 0s linear infinite;\n      position: absolute;\n      content: '';\n      width: 50px;\n      height: 50px;\n      top: 31px;\n      margin: 0 auto;\n      border-radius: 50%;\n      z-index: 0;\n    }\n    @-webkit-keyframes blow {\n      0% {\n        box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.1);\n        opacity: 1;\n        -webkit-transform: scale3d(1, 1, 0.5);\n        transform: scale3d(1, 1, 0.5);\n      }\n      50% {\n        box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.1);\n        opacity: 1;\n        -webkit-transform: scale3d(1, 1, 0.5);\n        transform: scale3d(1, 1, 0.5);\n      }\n      100% {\n        box-shadow: 0 0 0 20px rgba(0, 0, 0, 0.1);\n        opacity: 0;\n        -webkit-transform: scale3d(1, 1, 0.5);\n        transform: scale3d(1, 1, 0.5);\n      }\n    }\n    @keyframes blow {\n      0% {\n        box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.1);\n        opacity: 1;\n        -webkit-transform: scale3d(1, 1, 0.5);\n        transform: scale3d(1, 1, 0.5);\n      }\n      50% {\n        box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.1);\n        opacity: 1;\n        -webkit-transform: scale3d(1, 1, 0.5);\n        transform: scale3d(1, 1, 0.5);\n      }\n      100% {\n        box-shadow: 0 0 0 20px rgba(0, 0, 0, 0.1);\n        opacity: 0;\n        -webkit-transform: scale3d(1, 1, 0.5);\n        transform: scale3d(1, 1, 0.5);\n      }\n    }\n    img {\n      width: 100%;\n      border-radius: 100%;\n    }\n  }\n  .profile-text {\n    padding: 5px 0px;\n    position: relative;\n    cursor: pointer;\n    white-space: nowrap;\n    > a {\n      color: $white !important;\n      width: 100%;\n      padding: 6px 30px;\n      background: rgba(0, 0, 0, 0.5);\n      display: block;\n      white-space: nowrap;\n      &:after {\n        position: absolute;\n        right: 20px;\n        top: 20px;\n      }\n    }\n  }\n}\n\n/*============================================================== \n Small Desktop & above all (1024px) \n ============================================================== */\n\n@media (min-width: 1024px) {\n  .minisidebar #snav {\n    .mat-drawer-backdrop {\n      display: none;\n    }\n    &.mat-sidenav ~ .mat-drawer-content {\n      margin-left: $mini-sidebar-width;\n    }\n    &.mat-sidenav {\n      -webkit-transition: width 0.2s cubic-bezier(0.25, 0.8, 0.25, 1),\n        transform 0.2s cubic-bezier(0.25, 0.8, 0.25, 1) !important;\n      transition: width 0.2s cubic-bezier(0.25, 0.8, 0.25, 1),\n        transform 0.2s cubic-bezier(0.25, 0.8, 0.25, 1) !important;\n    }\n    &.mat-sidenav:not(:hover) {\n      width: $mini-sidebar-width;\n      .sub-menu,\n      .dd-icon,\n      .selected .sub-item {\n        display: none;\n        visibility: hidden;\n      }\n      .user-profile {\n        .profile-img {\n          margin-left: 15px;\n        }\n        .profile-text {\n          visibility: hidden;\n        }\n      }\n      .saperator span {\n        overflow: hidden;\n        text-overflow: ellipsis;\n        width: 40px;\n        display: block;\n      }\n      .mat-list-item > .mat-list-item-content > a {\n        padding-right: 0;\n        padding-left: 0;\n        text-align: center;\n      }\n      .mat-list-item > .mat-list-item-content > a > span:not(.menu-badge),\n      .mat-list-item > .mat-list-item-content > a > .menu-caret {\n        display: none;\n      }\n      .mat-list-item > .mat-list-item-content > a > span.menu-badge {\n        position: absolute;\n        top: 10px;\n        right: 16px;\n      }\n      .mat-list-item > .mat-list-item-content > a > .material-icons {\n        width: $mini-sidebar-width !important;\n        padding: 0;\n        margin: 0;\n      }\n    }\n  }\n  [dir='rtl'] {\n    app-sidebar {\n      display: block;\n      overflow: hidden;\n    }\n    .minisidebar #snav.mat-sidenav ~ .mat-drawer-content {\n      margin-left: 0px !important;\n      margin-right: $mini-sidebar-width !important;\n    }\n  }\n}\n\n.sidebar-closed #snav {\n  &.mat-sidenav ~ .mat-drawer-content {\n    margin-left: 0px !important;\n  }\n}\n","/*\nTemplate Name: Material Pro Admin\nAuthor: Themedesigner\nEmail: niravjoshi87@gmail.com\nFile: scss\n*/\n\n$mini-sidebar-width: 80px;\n$sidebar: #424242;\n$sidebar-text: #8999a0;\n$sidebar-icons: #909394;\n\n/*******************\nMain sidebar\n******************/\n\n#snav.dark-sidebar {\n    background: $sidebar;\n    .mat-list-item {\n        .mat-list-item-content {\n            &:hover {\n                background: none;\n                > a {\n                    color: $white;\n                }\n            }\n        }\n        a {\n            color: $sidebar-text;\n            mat-icon {\n                color: $sidebar-icons;\n            }\n        }\n        &.selected > .mat-list-item-content > a {\n            background: $themecolor-alt;\n            border-radius: $radius;\n            color: $white;\n            mat-icon {\n                color: $white;\n            }\n        }\n        &.selected .sub-item .mat-list-item-content a.selected {\n            background: transparent;\n            color: $white;\n        }\n    }\n}\n\n\n/*******************\nuse profile section\n******************/\n\n.user-profile {\n    .profile-text {\n        > a {\n            color: $white!important;\n        }\n    }\n}","/*\nTemplate Name: Material Pro Admin\nAuthor: Themedesigner\nEmail: niravjoshi87@gmail.com\nFile: scss\n*/\n/*============================================================== \n For Laptop & above all (1650px) \n ============================================================== */\n\n@media(min-width:1650px) {\n    .main-container.boxed{\n        max-width: 1920px;\n        margin: 0 auto;\n    }\n}\n/*============================================================== \n For Laptop & above all (1370px) \n ============================================================== */\n\n@media(max-width:1600px) {\n     .main-container.boxed{\n        max-width: 1300px;\n        margin: 0 auto;\n    } \n}\n\n\n/*-- ============================================================== \n Small Desktop & above all (1024px) \n ============================================================== */\n\n@media(min-width:1024px) {\n  .card-group{\n    padding: 0 15px;\n    display: flex;\n    flex-flow:row wrap;\n    .mat-card{\n       margin: 15px 0px !important;\n       flex: 1 0 0%;\n        border-left:1px solid $border;\n            &:first-child{\n                border:0px;\n            }\n        }\n    } \n\n    \n}   \n\n@media(max-width:1023px) {\n    .card-group{\n        margin: 15px 0;\n        .mat-card{\n            margin: 0 15px;\n            border-top:1px solid $border;\n            &:first-child{\n                border:0px;\n            }\n        }\n    }\n}\n\n\n/*-- ============================================================== \n Ipad & above all(768